*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

body {
  font-family: var(--font-inter);
  background-color: var(--dark-bg);
  color: var(--dark-text-1);
}

:root {
  --font-inter: "Inter", sans-serif;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 3px;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --dark-toggle-start: hsl(210, 78%, 56%);
  --dark-toggle-end: hsl(146, 68%, 55%);
  --light-toggle: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card-bg: hsl(228, 28%, 20%);
  --dark-text-1: hsl(0, 0%, 100%);
  --dark-text-2: hsl(228, 34%, 66%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card-bg: hsl(227, 47%, 96%);
  --light-text-1: hsl(230, 17%, 14%);
  --light-text-2: hsl(228, 12%, 44%);
}

.container {
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
  padding: 0 1.5625rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--dark-text-2);
}
.toggle {
  border: none;
}
.toggle input[type=radio] {
  width: 50%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}
.toggle__switcher {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: var(--dark-bg);
}
.card {
  padding: 1.25rem;
}